<template>
    <yd-layout class="bindingAccount">
        <div class="noMain" v-show="accountFlag==false&&inputFlag==false">
            <div class="noAccount">您还没有绑定账户哦~</div>
        </div>
        <div class="inputNum" v-show="inputFlag">
            <div class="inputText">请输入您云闪付app账户绑定的手机号</div>
            <!-- <trsTextarea v-model="phoneNum"></trsTextarea> -->
            <yd-cell-group>
                <yd-cell-item>
                    <yd-input slot="right" v-model="phoneNum" ref="phoneNum" required :show-success-icon="false" :show-error-icon="false" regex="mobile"></yd-input>
                </yd-cell-item>
            </yd-cell-group>
        </div>
        <div class="mainCol" v-show="accountFlag==true&&inputFlag==false">
            <yd-flexbox v-for="(value, key, index) in phoneList" :key="index">
                <yd-flexbox-item>{{key}}</yd-flexbox-item>
                <!-- <yd-flexbox-item>已绑卡：3</yd-flexbox-item> -->
                <div class="state1" v-if="value=='已通过'">{{value}}</div>
                <div class="state3" v-if="value=='验证中'">{{value}}</div>
                <div class="state2" v-if="value=='未通过'">{{value}}</div>
            </yd-flexbox>
        </div>
        <div class="ts">若账号绑定未通过，可咨询人力资源部杨正茂：021-20638731</div>
        <yd-tabbar slot="tabbar">
            <yd-button class="button" size="large" type="primary" bgcolor="#59a5fd" color="#fff" @click.native="inputFlag=true" v-show="accountFlag==false&&inputFlag==false">绑定云闪付账户</yd-button>
            <yd-button class="button" size="large" type="primary" bgcolor="#59a5fd" color="#fff" @click.native="inputFlag=true" v-show="accountFlag==true&&inputFlag==false">绑定新的云闪付账户</yd-button>
            <yd-button class="button" size="large" type="primary" bgcolor="#59a5fd" color="#fff" @click.native="inputphone()" v-show="inputFlag">确定</yd-button>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            accountFlag: true,
            inputFlag: false,
            phoneList: {},
            phoneNum: ''
        }
    },
    mounted() {
        let vm = this;
        vm.app_phone();
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        // 账号列表
        app_phone() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/empproexperience/app_phone', {
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                console.log(res);
                vm.phoneList = res.jsonData;
                if (Object.keys(vm.phoneList).length == 0) {
                    vm.accountFlag = false;
                }
            });
        },
        // 綁定手机号
        inputphone() {
            let vm = this;
            if (vm.phoneNum == '') {
                // vm.accountFlag = true;
                vm.inputFlag = false;
            } else {
                let phone = this.$refs.phoneNum;
                if (phone.valid) {
                    vm.getM(vm.conf.portalMobile + '/empproexperience/inputphone', {
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'phone': vm.phoneNum
                    }, function(res) {
                        if (res.isSuccess == false) {
                            vm.$dialog.toast({
                                mes: res.errorMessage,
                                timeout: 2000
                            });
                        } else {
                            vm.app_phone();
                            vm.accountFlag = true;
                            vm.inputFlag = false;
                            vm.phoneNum = '';
                            vm.$dialog.toast({
                                mes: '手机号验证中',
                                timeout: 2000
                            });
                            console.log(res);
                        }
                    });
                } else {
                    vm.$dialog.toast({
                        mes: '请输入正确的手机号',
                        timeout: 2000
                    });
                }
            }
        }
    }
}
</script>
<style lang="less" scoped>
.noMain {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-color: #fff;
    .noAccount {
        height: 3.4rem;
        width: 3rem;
        background: url(../../../static/images/experience/Group 3@2x.png) no-repeat;
        background-size: 2.4rem;
        background-position: .3rem 0;
        font-size: .3rem;
        padding-top: 3rem;
        color: #999;
    }
}

.inputNum {
    padding: 0 .4rem;
    padding-top: 5.2rem;
    height: 100%;
    background-color: #fff;
    .inputText {
        font-size: .26rem;
        color: #999;
    }
    .trsTextarea {
        height: 1.2rem;
        border-bottom: 1px solid #e0e0e0;
        line-height: 1.2rem;
        font-size: .26rem;
        color: #999;
    }
}

.mainCol {
    background-color: #fff;
    .state1 {
        font-size: .32rem;
        color: #11bf34;
        background: url(../../../static/images/experience/rz@3x.png) no-repeat;
        background-size: .4rem;
        padding-left: .68rem;
    }
    .state2 {
        font-size: .32rem;
        color: #ff8d2c;
        background: url(../../../static/images/experience/wtg@3x.png) no-repeat;
        background-size: .4rem;
        padding-left: .68rem;
    }
    .state3 {
        font-size: .32rem;
        color: #ff4b4b;
        background: url(../../../static/images/experience/yzz@3x.png) no-repeat;
        background-size: .4rem;
        padding-left: .68rem;
    }
}

.yd-cell-item {
    padding-left: 0;
    border-bottom: 1px solid #e0e0e0;
}

.ts {
    font-size: .24rem;
    color: #999;
    text-align: center;
    line-height: .5rem;
    padding: 0 .36rem;
    padding-top: .3rem;
}
</style>


